import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import './SeminarList.css'; //  

function SeminarList() {
  const [seminar, setSeminar] = useState([]);

  useEffect(() => {
    axios.get('http://13.51.233.141/api/seminar')
      .then(response => setSeminar(response.data))
      .catch(error => console.error(error));
  }, []);

  return (
    <div className="seminar-list-container">
      <h1></h1>
      <ul className="seminar-list">
        {seminar.map(item => (
          <li key={item._id} className="seminar-item">
            <p><strong>:</strong> {item.title}</p>
            <p><strong>?:</strong> {item.when}</p>
            <p><strong>?:</strong> {item.where}</p>
            <p><strong>:</strong> {item.cost}</p>
            <Link to={`/editseminar/${item._id}`} state={{ item }} className="edit-link"></Link>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default SeminarList;
