import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './UsersList.css';

function UsersList() {
    const [users, setUsers] = useState([]);
    const [comments, setComments] = useState({});

    useEffect(() => {
        axios.get('http://13.51.233.141/api/users')
            .then(response => setUsers(response.data))
            .catch(error => console.error(error));
    }, []);

    const formatDate = (dateString) => {
        const date = new Date(dateString);
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
    };

    const handleCommentChange = (userId, value) => {
        setComments(prevComments => ({ ...prevComments, [userId]: value }));
        
        // Send the PUT request to update the comment
        axios.put(`http://13.51.233.141/api/users/${userId}/coment`, { coment: value })
            .then(response => console.log(response.data))
            .catch(error => console.error(error));
    };

    return (
        <div className="users-list-container">
            <h1> </h1>
            <table className="users-table">
                <thead>
                    <tr>
                        <th> </th>
                        <th>  </th>
                        <th></th>
                        <th></th>
                        <th> </th>
                        <th> </th>
                        <th></th>
                        <th> </th>
                        <th>   1</th>
                        <th>   2</th>
                        <th>   3</th>
                        <th>   </th>
                        <th>   1</th>
                        <th>   2</th>
                        <th>   3</th>
                        <th>   </th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {users.map(user => (
                        <tr key={user._id}>

                            <td style={{ width: "100px" }}>{formatDate(user.arrival_date) || 'N/A'}</td>
                            <td style={{ width: "100px" }}>{user.last_interaction_format || 'N/A'}</td>
                            <td >{user.first_name || 'N/A'}</td>
                            <td >{user.last_name || 'N/A'}</td>
                            <td >{user.username || 'N/A'}</td>
                            <td >{user.contact || 'N/A'}</td>
                            <td >{user.first_question === 'want_to_participate_in_events' ? '   ' : user.first_question === 'want_to_be_rehabilitologist' ? '  ': user.first_question === 'want_to_upgrade_qualification' ? '  ':''}</td>
                            <td >{user.second_question || 'N/A'}</td>
                            <td  className={user.payment_status_module_1 === 'Paid' ? '' : 'not-paid'}>
                                {user.payment_status_module_1 === 'Paid' ? '' : ' '}
                            </td>
                            <td data-label="Payment Status Module 2" className={user.payment_status_module_2 === 'Paid' ? '' : 'not-paid'}>
                                {user.payment_status_module_2 === 'Paid' ? '' : ' '}
                            </td>
                            <td  className={user.payment_status_module_3 === 'Paid' ? '' : 'not-paid'}>
                                {user.payment_status_module_3 === 'Paid' ? '' : ' '}
                            </td>
                            <td className={user.payment_status_all_course === 'Paid' ? '' : 'not-paid'}>
                                {user.payment_status_all_course === 'Paid' ? '' : ' '}
                            </td>
                            <td>{user.date_learn_module_1 || 'N/A'}</td>
                            <td>{user.date_learn_module_2 || 'N/A'}</td>
                            <td>{user.date_learn_module_3 || 'N/A'}</td>
                            <td>{user.date_learn_all_course || 'N/A'}</td>
                            <td data-label="Comments">
                                <textarea
                                    value={comments[user._id] || ''}
                                    onChange={(e) => handleCommentChange(user._id, e.target.value)}
                                />
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}

export default UsersList;
