import React, { useState, useEffect } from 'react';
import CryptoJS from 'crypto-js';
import axios from 'axios';
import './styles.css'; // Create a CSS file for styling

const LiqPayPay = ({
    publicKey,
    privateKey,
    amount,
    currency,
    description,
    orderId,
    subscribePeriodicity,
}) => {
    const [unsubsID, setUnsubsId] = useState(null);
    const [subscribeDate, setSubscribeDate] = useState(null);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [modalSubscribe, setModalSubscribe] = useState(false);
    const [loading, setLoading] = useState(true);

    const currentDate = new Date();
    const nextMonthDate = new Date();

    useEffect(() => {
        axios.get('http://13.51.233.141/api/getOrderId')
            .then(response => {
                paymentStatus('check-payment-status', response.data[0].order_id);
                setUnsubsId(response.data[0].order_id);
            })
            .catch(error => console.error(error))
            .finally(() => setLoading(false)); // Set loading to false after data is fetched
    }, []);

    nextMonthDate.setMonth(currentDate.getMonth() + 1);

    const subscribeDateStart = currentDate.toISOString().slice(0, 19).replace('T', ' ');

    const generatePaymentURL = () => {
        const liqpayData = {
            public_key: publicKey,
            version: '3',
            action: 'subscribe',
            subscribe_periodicity: subscribePeriodicity,
            amount,
            currency,
            description,
            order_id: orderId,
            sandbox: 'sandbox'
        };

        const liqpayDataStr = btoa(JSON.stringify(liqpayData));
        const signString = `${privateKey}${liqpayDataStr}${privateKey}`;
        const liqpaySignature = CryptoJS.SHA1(signString).toString(CryptoJS.enc.Base64);

        return `https://www.liqpay.ua/api/3/checkout?data=${liqpayDataStr}&signature=${liqpaySignature}`;
    };

    const paymentStatus = async (way, orderId) => {
        try {
            const response = await axios.post(`http://13.51.233.141/api/${way}`, { order_id: orderId });
            if (response.data.success) {
                setSubscribeDate(response.data.dateStart);
                setIsModalOpen(false);
            } else {
                setIsModalOpen(true);
            }
        } catch (error) {
            console.error('Error checking payment status:', error);
            alert('An error occurred while checking payment status.');
        }
    };

    const updateOrderId = async () => {
        try {
            const response = await fetch('http://13.51.233.141/api/update-order', {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    userName: 'arina',
                    orderId: orderId
                }),
            });

            const data = await response.json();

            if (response.ok) {
                console.log('Order ID updated successfully:', data.message);
            } else {
                console.error('Failed to update Order ID:', data.message);
            }
        } catch (error) {
            console.error('Error updating Order ID:', error);
        }
    };

    const paymentURL = generatePaymentURL();

    return (
        <div>
            {loading ? (
                <div className="spinner"></div>
            ) : (
                <>
                    <div onClick={()=>setModalSubscribe(true)} className='modal-button'> </div>
                    <p>
                        {modalSubscribe ? (
                            <div className="modal-overlay" onClick={()=>setModalSubscribe(false)}>
                                <div className="modal" onClick={(e) => e.stopPropagation()}>
                                    <div>
                                         <br />
                                        , {new Date(subscribeDate).getDate()}  <br />
                                          : <strong>15$</strong>
                                        <div className='modal-button'
                                            onClick={() => {
                                                paymentStatus('unsubs', unsubsID);
                                                setModalSubscribe(false)
                                                setIsModalOpen(true);
                                            }}
                                        >
                                            
                                        </div>
                                    </div>
                                </div>
                            </div>

                        ) : (
                            <></>
                        )}
                    </p>
                    {isModalOpen && (
                        <div className="modal-overlay">
                            <div className="modal">
                                <div>      .   ,  , </div>
                                <a href={paymentURL} onClick={updateOrderId} target="_blank" rel="noopener noreferrer">
                                    <button className="modal-button">
                                        
                                    </button>
                                </a>
                            </div>
                        </div>
                    )}
                </>
            )}
        </div>
    );
};

export default LiqPayPay;
